import React, { useEffect, useState } from 'react'
import {BlogItem, Button, Gap} from '../../components'
import './home.scss'
import {useHistory} from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { setDataBlog } from '../../config/redux/action'
import { confirmAlert } from 'react-confirm-alert'
import 'react-confirm-alert/src/react-confirm-alert.css'
import Axios from 'axios'
import toast, { Toaster } from 'react-hot-toast'

const Home = () => {
    const [counter, setCounter] = useState(1)
    const {dataBlog, page} = useSelector(state => state.homeReducer);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(setDataBlog(counter))
    }, [counter, dispatch])

    const history = useHistory()

    const previous = () => {
        setCounter(counter <= 1 ? 1 : counter - 1)
    }

    const next = () => {
        setCounter(counter === page.totalPage ? page.totalPage : counter + 1)
    }

    const confirmDelete = (id) => {
        confirmAlert({
            title: 'Confirm to delete',
            message: 'Apakah Anda yakin menghapus post ini?.',
            buttons: [
                {
                    label: 'Ya',
                    onClick: () => {
                        Axios.delete(`https://kopisob-api.herokuapp.com/v1/blog/post/${id}`)
                        .then(res => {
                            console.log('delete success ', res.data);
                            notify('Data berhasil di hapus')
                            dispatch(setDataBlog(counter))
                        })
                        .catch(err => {
                            console.log('err ', err);
                        })
                    }
                },
                {
                    label: 'Tidak',
                    onClick: () => console.log('user tidak setuju')
                }
            ]
        });
    }

    const notify = (message) => toast.success(message, {
        duration: 3000
    })

    return (
        <div className="home-page-wrapper">
            <div className="create-wrapper">
                <Button title="create blog" onClick={() => history.push('/create-blog')} />
            </div>
            <Gap height={20}/>
            <div className="content-wrapper">
                {dataBlog.map(blog => {
                    return (
                        <BlogItem
                            key={blog._id}
                            image={`https://kopisob-api.herokuapp.com/${blog.image}`}
                            title={blog.title}
                            name={blog.author.name}
                            date={blog.createdAt}
                            body={blog.body}
                            _id={blog._id}
                            onDelete={confirmDelete}
                        />
                    );
                })}
            </div>
            <div className="pagination">
                <Button title="previous" onClick={previous} />
                <Gap width={20} />
                <p className="text-page">{page.currentPage} / {page.totalPage}</p>
                <Gap width={20} />
                <Button title="next" onClick={next} />
            </div>
            <Gap height={20} />
            <Toaster />
        </div>
    )
}

export default Home;